body {
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  overflow-x: hidden; }

p {
  font-size: 20px;
  &.small {
    font-size: 16px; } }

a {
  color: #18BC9C;
  &:hover, &:focus, &:active, &.active {
    color: #18BC9C; } }

h1, h2, h3, h4, h5, h6 {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-weight: 700; }

hr {
  &.star-light, &.star-primary {
    padding: 0;
    border: none;
    border-top: solid 5px;
    text-align: center;
    max-width: 250px;
    margin: 25px auto 30px; }
  &.star-light:after, &.star-primary:after {
    content: "\f005";
    font-family: FontAwesome;
    display: inline-block;
    position: relative;
    top: -0.8em;
    font-size: 2em;
    padding: 0 0.25em; }
  &.star-light {
    border-color: white;
    &:after {
      background-color: #18BC9C;
      color: white; } }
  &.star-primary {
    border-color: #2C3E50;
    &:after {
      background-color: white;
      color: #2C3E50; } } }

.img-centered {
  margin: 0 auto; }

header {
  text-align: center;
  background: #18BC9C;
  background-image: url("img/background.jpg");
  color: white;
  .container {
    padding-top: 100px;
    padding-bottom: 50px; }
  img {
    display: block;
    margin: 0 auto 20px; }
  .intro-text {
    .name {
      display: block;
      font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
      text-transform: uppercase;
      font-weight: 700;
      font-size: 2em; }
    .skills {
      font-size: 1.25em;
      font-weight: 300; } } }

@media (min-width: 768px) {
  header {
    .container {
      padding-top: 200px;
      padding-bottom: 100px; }
    .intro-text {
      .name {
        font-size: 4.75em; }
      .skills {
        font-size: 1.75em; } } } }

.navbar-custom {
  background: #2C3E50;
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  border: none;
  .navbar-brand {
    color: white;
    &:hover, &:focus, &:active, &.active {
      color: white; } }
  .navbar-nav {
    letter-spacing: 1px;
    li {
      a {
        color: white;
        &:hover {
          color: #18BC9C; }
        &:focus, &:active {
          color: white; } }
      &.active a {
        color: white;
        background: #18BC9C;
        &:hover, &:focus, &:active {
          color: white;
          background: #18BC9C; } } } }
  .navbar-toggle {
    color: white;
    text-transform: uppercase;
    font-size: 10px;
    border-color: white;
    &:hover, &:focus {
      background-color: #18BC9C;
      color: white;
      border-color: #18BC9C; } } }

@media (min-width: 768px) {
  .navbar-custom {
    padding: 25px 0;
    -webkit-transition: padding 0.3s;
    -moz-transition: padding 0.3s;
    transition: padding 0.3s;
    .navbar-brand {
      font-size: 2em;
      -webkit-transition: all 0.3s;
      -moz-transition: all 0.3s;
      transition: all 0.3s; }
    &.affix {
      padding: 10px 0;
      .navbar-brand {
        font-size: 1.5em; } } } }

section {
  padding: 100px 0;
  h2 {
    margin: 0;
    font-size: 3em; }
  &.success {
    background: #08092d;
    color: white; } }

@media (max-width: 767px) {
  section {
    padding: 75px 0;
    &.first {
      padding-top: 75px; } } }

#portfolio {
  .portfolio-item {
    margin: 0 0 15px;
    right: 0;
    .portfolio-link {
      display: block;
      position: relative;
      max-width: 400px;
      margin: 0 auto;
      .caption {
        background: rgba(24, 188, 156, 0.9);
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all ease 0.5s;
        -webkit-transition: all ease 0.5s;
        -moz-transition: all ease 0.5s;
        &:hover {
          opacity: 1; }
        .caption-content {
          position: absolute;
          width: 100%;
          height: 20px;
          font-size: 20px;
          text-align: center;
          top: 50%;
          margin-top: -12px;
          color: white;
          i {
            margin-top: -12px; }
          h3, h4 {
            margin: 0; } } } } }
  * {
    z-index: 2; } }

@media (min-width: 767px) {
  #portfolio .portfolio-item {
    margin: 0 0 30px; } }

.floating-label-form-group {
  position: relative;
  margin-bottom: 0;
  padding-bottom: 0.5em;
  border-bottom: 1px solid #eeeeee;
  input, textarea {
    z-index: 1;
    position: relative;
    padding-right: 0;
    padding-left: 0;
    border: none;
    border-radius: 0;
    font-size: 1.5em;
    background: none;
    box-shadow: none !important;
    resize: none; }
  label {
    display: block;
    z-index: 0;
    position: relative;
    top: 2em;
    margin: 0;
    font-size: 0.85em;
    line-height: 1.764705882em;
    vertical-align: middle;
    vertical-align: baseline;
    opacity: 0;
    -webkit-transition: top 0.3s ease,opacity 0.3s ease;
    -moz-transition: top 0.3s ease,opacity 0.3s ease;
    -ms-transition: top 0.3s ease,opacity 0.3s ease;
    transition: top 0.3s ease,opacity 0.3s ease; }
  &:not(:first-child) {
    padding-left: 14px;
    border-left: 1px solid #eeeeee; } }

.floating-label-form-group-with-value label {
  top: 0;
  opacity: 1; }

.floating-label-form-group-with-focus label {
  color: #18BC9C; }

form .row:first-child .floating-label-form-group {
  border-top: 1px solid #eeeeee; }

