.portfolio-modal {
  .modal-content {
    border-radius: 0;
    background-clip: border-box;
    -webkit-box-shadow: none;
    box-shadow: none;
    border: none;
    min-height: 100%;
    padding: 100px 0;
    text-align: center;
    h2 {
      margin: 0;
      font-size: 3em; }
    img {
      margin-bottom: 30px; }
    .item-details {
      margin: 30px 0; } }
  .close-modal {
    position: absolute;
    width: 75px;
    height: 75px;
    background-color: transparent;
    top: 25px;
    right: 25px;
    cursor: pointer;
    &:hover {
      opacity: 0.3; }
    .lr {
      height: 75px;
      width: 1px;
      margin-left: 35px;
      background-color: #2C3E50;
      transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      /* IE 9 */
      -webkit-transform: rotate(45deg);
      /* Safari and Chrome */
      z-index: 1051;
      .rl {
        height: 75px;
        width: 1px;
        background-color: #2C3E50;
        transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        /* IE 9 */
        -webkit-transform: rotate(90deg);
        /* Safari and Chrome */
        z-index: 1052; } } }
  .modal-backdrop {
    opacity: 0;
    display: none; } }

#skipnav a {
  padding: 6px;
  position: absolute;
  top: -40px;
  left: 0px;
  color: white;
  border-right: 1px solid white;
  border-bottom: 1px solid white;
  border-bottom-right-radius: 8px;
  background: transparent;
  transition: top 1s ease-out, background 1s linear;
  z-index: 2000;
  &:focus {
    position: absolute;
    left: 0px;
    top: 0px;
    background: #18BC9C;
    outline: 0;
    transition: top 0.1s ease-in, background 0.5s linear; } }

div#maincontent {
  outline: none; }

/*Testing Sroller */

.tech-slideshow {
  height: 200px;
  max-width: 800px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  > div {
    height: 200px;
    width: 2526px;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    transform: translate3d(0, 0, 0); }
  .mover-1 {
    animation: moveSlideshow 12s linear infinite; }
  .mover-2 {
    opacity: 0;
    transition: opacity 0.5s ease-out;
    background-position: 0 -200px;
    animation: moveSlideshow 15s linear infinite; }
  &:hover .mover-2 {
    opacity: 1; } }

@keyframes moveSlideshow {
  100% {
    transform: translateX(-66.6666%); } }


